import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'
import Show from '@/components/Show'
import Item from '@/components/Item'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/:id',
      name: 'Main',
      component: Main,
      children:[
        {
          path: '/',
          // redirect:{
          //   name: 'item'
          // }
          name: 'item',
          component: Item
        },
        {
          path: '/show/:num',
          name: 'Show',
          component: Show
        },
        {
          path: '/item',
          name: 'item',
          component: Item
        }
      ]
    },
    {
      path: '/',
      name: 'Main1',
      component: Main,
      children:[
        {
          path: '/',
          name: 'item1',
          component: Item
        },
        {
          path: '/show/:num',
          name: 'Show1',
          component: Show
        },
        {
          path: '/item',
          name: 'item1',
          component: Item
        }
      ]
    }
  ]
})



