<template>
    <div class="content">
        <div class="bar">
          <img class="bar_img" src="/static/images/t14.png" alt="">
        </div>
        <div class="item" @click="show_item('1')">
          <img class="item_img" src="/static/images/study.png" alt="">
          <div class="item_font">校园女生</div>
        </div>
        <div class="item" @click="show_item('2')">
          <img class="item_img" src="/static/images/work.png" alt="">
          <div class="item_font">职场OL</div>
        </div>
        <div class="item" @click="show_item('3')">
          <img class="item_img" src="/static/images/life.png" alt="">
          <div class="item_font">家庭主妇</div>
        </div>
        <div class="item" @click="show_item('4')">
          <img class="item_img" src="/static/images/money.png" alt="">
          <div class="item_font">交际名媛</div>
        </div>
        <div class="item" @click="show_item('5')">
          <img class="item_img" src="/static/images/share.png" alt="">
          <div class="item_font">招募管理</div>
        </div>
      </div>
</template>
<script>
export default {
    data () {
    return {
      need_flag : false,
      help_flag : false,
    }
  },
  methods:{
    show_need(){
      this.need_flag = !this.need_flag;
      this.help_flag = false;
    },
    show_help(){
      this.help_flag = !this.help_flag;
      this.need_flag = false;
    },
    show_item(id){
      this.$router.push({ path: `/show/${id}`})
    }
  }
}
</script>
<style scoped>
.content{
  display: flex;
  flex-direction: column;
  margin-top:25px;
  font-size: 14px;
  padding: 2px;
}
.content div{
  margin:2px;
}
.bar{
  height:110px;
  border: 1px;
  border-style: solid;
  border-color: white;
  border-radius: 2px;
  background-color: white;
  background-image: url("/static/images/3.png");
  background-size:100% 170px;
}
.bar_img{
  height:100px;
  width: 100%;
}
.item{
  height:80px;
  border: 1px;
  border-style: solid;
  border-color: white;
  border-radius: 5px;
  display: flex;
  flex-direction: row;
  cursor: pointer;
  background-image: url("/static/images/2.png");
  background-size:140% 100%;
  background-color:mediumpurple;
}
.item_img{
  width: 210px;
  height: 70px;
  margin: auto 3px;
}
.item_font{
  font-size:18px;
  line-height: 70px;
  flex-grow: 1;
}
</style>


