<template>
  <div class="main_class">
    <div class="centent_class">
      <div class="top">私密行动组</div>
      <router-view/>
      <div class="buttom">
        <div @click="show_reg">会员注册</div>
        <div @click="show_task">信息查询</div>
      </div>
      <!-- 注册页面 -->
      <div class="popwin" v-show="reg_flag">
        <div class="poptop">
          <div class="top_main">注册页面</div>
          <div class="top_close" @click="close_reg">关闭</div>
        </div>
        <div class="poptab">
          <div class="tab_container">
            <span class="reg_span">账号：</span>
            <input class="reg_item" type="text" value="" placeholder="输入账号">
          </div>
          <div class="tab_container">
            <span class="reg_span">性别：</span>
            <select class="reg_item">
              <option value ="famale">女</option>
              <option value="male">男</option>
            </select>
          </div>
          <div class="tab_container">
            <span class="reg_span">年龄：</span>
            <input class="reg_item" type="number" value="">
          </div>
          <div class="tab_container">
            <span class="reg_span">密码：</span>
            <input class="reg_item" type="text" value="" placeholder="输入密码">
          </div>
          <div class="tab_container">
            <span class="reg_span">密码：</span>
            <input class="reg_item" type="text" value="" placeholder="确认密码">
          </div>
          <div class="tab_container">
            <span class="reg_span">通讯：</span>
            <input class="reg_item" type="text" value="" placeholder="QQ号/微信号/手机号">
          </div>
          <div class="tab_container">
            <span class="reg_span">地址：</span>
            <input class="reg_item" type="text" value="" placeholder="工作或生活常驻地">
          </div>
          <div class="tab_container">
            <span class="reg_span">需求：</span>
            <select class="reg_item">
              <option class="item_op" value ="value1">校园女生</option>
              <option class="item_op" value ="value2">职场OL</option>
              <option class="item_op" value="value3">家庭主妇</option>
              <option class="item_op" value="value4">交际名媛</option>
              <option class="item_op" value="value4">应聘管理</option>
            </select>
          </div>
        </div>
        <div class="popbtn" @click="commit_reg">提交</div>
      </div>
      <!-- 完成页面 -->
      <div class="popwin" v-show="complate_flag">
        <div class="poptop">
          <div class="top_main">成功页面</div>
        </div>
        <div class="poptab">
          <div>已完成提交，感谢您的参与！。</div>
        </div>
        <div class="popbtn" @click="close_complate">关闭</div>
      </div>
      <!-- 任务页面 -->
      <div class="popwin" v-show="task_flag">
        <div class="poptop">
          <div class="top_main">任务页面</div>
          <div class="top_close" @click="close_task">关闭</div>
        </div>
        <div class="poptab">
          <div class="tab_container">
            <span class="reg_span">任务码：</span>
            <input class="reg_item" type="text" value="" placeholder="任务码">
          </div>
          <div>任务描述：</div>
          <div class="taskc_content">{{task_detail}}</div>
        </div>
        <div class="popbtn" @click="commit_task">查询</div>
      </div>
    </div>
  </div>
</template>

<script>
var storage=window.localStorage;

export default {
  data () {
    return {
      reg_flag : false,
      task_flag : false,
      complate_flag : false,
      reg_data : {},
      id : "",
      id2: "",
      task_detail: ""
    }
  },
  methods:{
    show_reg(){
      this.complate_flag = false
      this.task_flag = false;
      this.reg_flag = true;
    },
    show_task(){
      this.complate_flag = false
      this.reg_flag = false;
      this.task_flag = true;
    },
    show_comlate(){
      this.reg_flag = false;
      this.task_flag = false;
      this.complate_flag = true;
    },
    close_complate(){
      this.complate_flag = false;
    },
    close_reg(){
      this.reg_flag = false;
    },
    close_task(){
      this.task_flag = false;
    },
    commit_reg(){
      this.show_comlate()
    },
    commit_task(){
        this.$http.get("/static/jsondata/task.json")
            .then((res)=>{
                console.log(res.data)
                this.task_detail = res.data.taskdetail
            })
            .catch(function (error) {
                console.log(error);
        })
    }
  },
  mounted(){
    console.log(!this.$route.params.id)
    if(this.$route.params.id){
      this.id = this.$route.params.id;
    }else{
      this.id = "admin"
    }
    storage.setItem('userid',this.id);
    this.id2 = storage.getItem('userid');
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.main_class{
  position: relative;
  background-color:#002040;
  width:100%;
  height: 100%;
  color:white;
  font-size: 12px;
}
.centent_class{
  width:100%;
  height:100%;
  position:absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  background-color:#CC99CC;
}
.top{
  height:25px;
	position:fixed;
	left:0;
	top:0;
	right:0;
  background-color:#2f0000;
  line-height: 25px;
}
.buttom{
  height:40px;
	position:fixed;
	left:0;
	bottom:0;
	right:0;
  background-color: #2f0000;
  display: flex;
  flex-direction: row;
  border: 1px;
  border-style: solid;
  border-color: white;
  border-radius: 5px;
}
.buttom div{
  flex-grow: 1;
  line-height: 40px;
  cursor: pointer;
  box-shadow: 0 1px 3px #333333;
  text-shadow: 0px -1px 1px rgba(0, 0, 0, 0.8);
  color:#F1E1FF;
}
.popwin {
  position: fixed;
  height: 300px;
  left:0;
	top:0;
	right:0;
  background-color:slategray;
  display: flex;
  flex-direction: column;
  /* opacity: 0.9; */
}
.poptab {
  display:table;
  margin: 0 auto;
  text-align:left;
  height:240px;
  font-size: 12px;
}
.tab_container{
  margin:5px 0;
}
.reg_span{
  width: 200px;
}
.reg_item{
  border-width:1px;
  border-style:solid;
  border-radius:5px;
  border-color:#0094ff;
  height:20px;
  width:250px;
  font-size: 12px;
}
.item_op{
  font-size: 10px;
}
.poptop{
  text-align:center;
  background-color:#2f0000;
  height: 25px;
  display: flex;
  flex-direction: row;
}
.top_main{
  flex-grow: 12;
  line-height: 25px;
}
.top_close{
  flex-grow: 1;
  line-height: 25px;
}
.popbtn{
  text-align:center;
  background-color:#2f0000;
  line-height: 35px;
  height:35px;
  cursor: pointer;
}
.taskc_content{
  width: 300px;
  margin-top: 10px;
}

</style>



