<template>
    <div class="showmain">
        <div class="showtop">
            <div class="top1">{{data.title}}</div>
            <div class="top2" @click="goback()">返回</div>
        </div>
        <div class="showcontent">
            <div class="wbef" :class="{activated:isActivated}">
                <img class="mainimage" v-bind:src='show_image' alt="">
                {{bef}}
            </div>
            <div class="wimage">
                <div class="witem" v-for="(item,i) in data.roles" v-bind:key="i">
                    <img class="wimg" v-bind:src='item.img' alt="" @click="change(i)">
                    <div class="wz">{{item.name}}</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            flag:"",
            bef:"",
            datalist:{},
            show_flag:false,
            isActivated: false,
            show_image:"/static/images/item1.png",
            data:{title:"",titleimg:"",content:"",roles:[{name:"",detail:"",img:""}]},
         }
    },
    methods:{
        goback(){
            this.$router.go(-1)
        },
        change(i){
            this.bef = this.data.roles[i].detail;
            this.show_image = this.data.roles[i].img2;
            this.isActivated = true;
            // this.show_flag = true;
        },
        close(){
            this.show_flag = false;
        },
    },
    mounted(){
        this.$http.get("/static/jsondata/items.json")
            .then((res)=>{
                this.datalist = res.data
                this.flag = this.$route.params.num;
                if (this.flag == 1){
                    this.data = this.datalist.item1;
                }else if(this.flag == 2){
                    this.data = this.datalist.item2;
                }else if(this.flag == 3){
                    this.data = this.datalist.item3;
                }else if(this.flag == 4){
                    this.data = this.datalist.item4;
                }else if(this.flag == 5){
                    this.data = this.datalist.item5;
                }else{
                    consolt.log("end")
                }     
                this.bef = this.data.content   
            })
            .catch(function (error) {
            console.log(error);
        })
    }
}
</script>

<style scoped>
.showmain{
    margin-top:25px;
    font-size: 12px;
    display: flex;
    flex-direction: column;
    height: 90%;
    background-color:#cc99cc;
}
.showtop{
    display: flex;
    flex-direction: row;
    line-height: 30px;
    font-size: 18px;
    height: 30px;
}
.top1{
    flex-grow: 9;
    text-align: left;
    padding-left:5px;
}
.top2{
    flex-grow: 1;
    text-align: right;
    padding-right: 5px;
}
.item_img{
  width: 50px;
  height: 80px;
  margin: auto 5px;
}
.showcontent{
    flex-grow: 1;
    display:flex;
    flex-direction: column;
}
.wbef{
    text-align: left;
    background-color:mediumpurple;
    margin: 2px 3px;
    padding: 5px;
    border:1px;
    border-style:solid;
    border-radius: 5px;
    flex: 1;
    position: relative;
    line-height: 25px;
}
.wimage{
    display: flex;
    flex-direction: row;
    margin:2px;
    height: 100px;
    background-color:#cc99cc;
}
.mainimage{
    width: 150px;
    height: 200px;
    float: left;
    margin: 5px;
}
.witem{
    flex-direction: column;
    margin: auto auto;
}
.wimg{
    width: 55px;
    height: 60px;
    margin: auto 1px;
    cursor: pointer;
}
.wz{
    line-height: 20px;
}
.activated{
    background-color:blueviolet;
}
</style>



